.infinite-scroll-wrapper {
  position: relative;
  height: 100%;
  overflow: hidden;
}

.bottom-loading {
  height: 1.333333rem;
  line-height: 1.333333rem;
  text-align: center;
  color: #999;
}

.bottom-loading cite {
  display: none;
}

.bottom-loading.show cite {
  display: block;
}

.bottom-placeholder {
  position: relative;
  overflow: hidden;
}


