.tip-page .tip-page-header {
  padding: 0.64rem 0.666667rem 0.533333rem;
  background-color: #fff;
  text-align: center;
}

.tip-page .tip-page-header .tip-page-img {
  width: 1.6rem;
  height: 1.6rem;
  margin: auto auto;
  background-size: cover;
}

.tip-page .tip-page-header .img-success {
  background-image: url(../assets/img/tip/success2x.png);
  background: -webkit-image-set(url(../assets/img/tip/success2x.png) 2x, url(../assets/img/tip/success3x.png) 3x) cover;
  background: image-set(url(../assets/img/tip/success2x.png) 2x, url(../assets/img/tip/success3x.png) 3x) cover;
}

.tip-page .tip-page-header .img-error {
  background-image: url(../assets/img/tip/error2x.png);
  background: -webkit-image-set(url(../assets/img/tip/error2x.png) 2x, url(../assets/img/tip/error3x.png) 3x) cover;
  background: image-set(url(../assets/img/tip/error2x.png) 2x, url(../assets/img/tip/error3x.png) 3x) cover;
}

.tip-page .tip-page-header .tip-page-title {
  margin-top: 0.533333rem;
  margin-bottom: 0.133333rem;
  font-size: 17px;
  color: #272727;
}

[data-dpr="1"] .tip-page .tip-page-header .tip-page-title {
  font-size: 17px;
}

[data-dpr="2"] .tip-page .tip-page-header .tip-page-title {
  font-size: 34px;
}

[data-dpr="3"] .tip-page .tip-page-header .tip-page-title {
  font-size: 51px;
}

.tip-page .tip-page-header .tip-page-msg {
  font-size: 13px;
  color: #B5B5B5;
}

[data-dpr="1"] .tip-page .tip-page-header .tip-page-msg {
  font-size: 13px;
}

[data-dpr="2"] .tip-page .tip-page-header .tip-page-msg {
  font-size: 26px;
}

[data-dpr="3"] .tip-page .tip-page-header .tip-page-msg {
  font-size: 39px;
}

.tip-page .tip-page-contain {
  padding: 0 0.666667rem;
}

.tip-page .tip-page-button {
  margin-top: 1.066667rem;
}


